.banner-container {
    height: 700px;
    width: 100%;
    

    overflow: hidden;

    
}


.video-banner {
   position: absolute;
   top: 0;
   left: 0;
   
   display: block;

   width: 100%;
   
}


.text-banner-container {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    

    

    justify-content: center;
    align-items: center;

    
}

.title-banner {
    text-transform: uppercase;
    color: rgb(50, 106, 228);
    font-size: 58px;
    letter-spacing: 2px;
    font-family: 'Concert One', cursive;
}

.text-banner {
    
    color: rgb(50, 106, 228);
    font-size: 38px;
    letter-spacing: 2px;
    font-family: 'Concert One', cursive;
}

.banner-imageee {
    width: 7%;
    height: 15%;
    

    right: 0;
    bottom: 0;
    

    position: absolute; 
}

.image-banneeer {
    height: 120px;
    

}

@media only screen and (max-width: 1000px) {
    .banner-container {
        display: none;
    }
}